import * as React from "react"
import { createContext, PropsWithChildren, useEffect, useState } from "react"

export const GoogleContext = createContext({
  isSignedIn: false,
  signIn: () => {},
  signOut: () => {},
})

export const GoogleContextWrapper = ({ children }: PropsWithChildren<{}>) => {
  const googleContextValue = useGoogleAuth()
  return (
    <GoogleContext.Provider value={googleContextValue}>
      {children}
    </GoogleContext.Provider>
  )
}

function useGoogleAuth() {
  const [isSignedIn, setIsSignedIn] = useState(false)

  useEffect(() => {
    const script = document.createElement("script")

    script.src = "https://apis.google.com/js/api.js"
    script.async = true
    script.onload = handleClientOnLoad

    document.body.appendChild(script)

    return () => {
      document.body.removeChild(script)
    }
  }, [])

  function handleClientOnLoad() {
    gapi.load("client:auth2", initClient)
  }

  function initClient() {
    gapi.client
      .init({
        clientId:
          "1092559866103-qqnunpnnnrqf0s15cvsn5l4s4uvc4hev.apps.googleusercontent.com",
        discoveryDocs: [
          "https://sheets.googleapis.com/$discovery/rest?version=v4",
        ],
        scope: "https://www.googleapis.com/auth/spreadsheets",
      })
      .then(
        () => {
          const GoogleAuth = gapi.auth2.getAuthInstance()
          GoogleAuth.isSignedIn.listen(updateSignInStatus)
          updateSignInStatus(gapi.auth2.getAuthInstance().isSignedIn.get())
        },
        (error) => {
          console.error(error)
        }
      )
  }

  function updateSignInStatus(isSignedIn: boolean) {
    setIsSignedIn(isSignedIn)
  }

  function signIn() {
    gapi.auth2.getAuthInstance().signIn()
  }

  function signOut() {
    gapi.auth2.getAuthInstance().signOut()
  }

  return { isSignedIn, signIn, signOut }
}
