import { Dispatch, useEffect, useState } from "react"
import * as CryptoJS from "crypto-js"
import * as ynab from "ynab"

const spreadsheetId = "1-QPiBrLPQifCL19hLGQh_HQg4lYTqB1z2pLLFIrM0BM"

export function useExpensesAndSavings(isSignedIn: boolean, password: string) {
  const [expenses, setExpenses] = useState<Expense[]>([])
  const [savings, setSavings] = useState(0)

  useEffect(() => {
    if (isSignedIn && password.length > 0) {
      loadExpenses(setExpenses)
      loadSavings(password, setSavings)
    } else {
      setExpenses([])
      setSavings(0)
    }
  }, [isSignedIn, password])

  return { expenses, savings }
}

async function loadExpenses(setExpenses: Dispatch<Expense[]>) {
  const response = await gapi.client.sheets.spreadsheets.values.get({
    spreadsheetId,
    range: "Hankinnat!A2:B100",
  })
  setExpenses(
    response.result.values.map(([amount, name]) => ({
      name,
      amount: parseInt(amount),
    }))
  )
}

async function loadSavings(password: string, setSavings: Dispatch<number>) {
  const response = await gapi.client.sheets.spreadsheets.values.get({
    spreadsheetId,
    range: "YNAB-avain!A1:A1",
  })
  const encryptedYnabApiToken = response.result.values[0][0]
  const bytes = CryptoJS.AES.decrypt(encryptedYnabApiToken, password)
  const ynabApiToken = bytes.toString(CryptoJS.enc.Utf8)
  const ynabAPI = new ynab.API(ynabApiToken)
  const category = await ynabAPI.categories.getCategoryById(
    "1bcc0db2-57f1-4426-a500-6210c9a4e72f",
    "87637d76-3d4a-46fb-928f-ac6e6a71737a"
  )
  setSavings(category.data.category.balance / 1000)
}
