import * as React from "react"
import { useContext, useState } from "react"
import { GoogleContext } from "./GoogleContext"
import { useExpensesAndSavings } from "./useExpensesAndSavings"

export const Saastot = () => {
  const { isSignedIn, signIn, signOut } = useContext(GoogleContext)
  const [password, setPassword] = useState("")
  const [amountToSize, setAmountToSize] = useState(6)

  const { expenses, savings } = useExpensesAndSavings(isSignedIn, password)

  function onSignOut() {
    setPassword("")
    signOut()
  }

  if (!isSignedIn || password.length === 0) {
    return (
      <div style={{ padding: "0.5rem 1rem" }}>
        <h1 style={{ fontSize: "calc(0.8rem + 3vw)", marginBottom: "0.5rem" }}>
          Säästöt
        </h1>
        <input
          type="password"
          value={password}
          onChange={(event) => setPassword(event.target.value)}
          style={{ display: "block", width: "100%", marginBottom: "0.5rem" }}
        />
        <button onClick={signIn}>Kirjaudu sisään</button>
      </div>
    )
  }

  const total = expenses.reduce((total, expense) => total + expense.amount, 0)
  return (
    <div>
      <header
        style={{
          display: "flex",
          justifyContent: "space-between",
          background: "white",
          boxShadow: "0 0 6px rgba(0,0,0,0.2)",
          position: "sticky",
          padding: "0.5rem 1rem",
          gap: "0.5rem",
          top: 0,
          zIndex: 1000,
        }}
      >
        <h1 style={{ fontSize: "calc(0.8rem + 3vw)" }}>Säästöt</h1>
        <input
          style={{ flexGrow: 1 }}
          type="range"
          min={3}
          max={20}
          step={0.1}
          value={amountToSize}
          onChange={(event) => setAmountToSize(parseFloat(event.target.value))}
        />
        <button onClick={onSignOut}>Kirjaudu ulos</button>
      </header>
      <div
        style={{
          display: "flex",
          background: "dimgray",
        }}
      >
        <div style={{ width: "75%" }}>
          {expenses.map(({ name, amount }, index) => {
            const color = 100 + Math.round((index % 5) * 31)
            return (
              <div
                key={index}
                style={{
                  display: "flex",
                  color: "white",
                  alignItems: "center",
                  gap: "1rem",
                  justifyContent: "space-between",
                  height: amount / amountToSize,
                  background: `rgb(${color}, 0, 0)`,
                  boxSizing: "border-box",
                  position: "relative",
                  fontSize:
                    amount / amountToSize > 20
                      ? "1rem"
                      : amount / amountToSize / 2,
                  padding: "1px 1rem 0 1rem",
                  borderBottom: "1px solid rgba(0,0,0,0.05)",
                }}
              >
                <div>{name}</div>
                <div>{amount}€</div>
              </div>
            )
          })}
        </div>
        <div
          style={{
            alignSelf: "flex-start",
            height: "100%",
            width: "25%",
            display: "flex",
            alignItems: "flex-start",
          }}
        >
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              gap: "0.4rem",
              color: "#222",
              alignItems: "center",
              justifyContent: "center",
              height: savings / amountToSize,
              background: "lime",
              width: "100%",
              padding: "0 1rem 2px 1rem",
              position: "relative",
            }}
          >
            <div>Säästössä</div>
            <div
              style={{
                fontWeight: "600",
                padding: "0.1rem 0.3rem",
                background: "white",
                borderRadius: 10,
                color: "green",
              }}
            >
              {Math.floor(savings)}€
            </div>
            <div style={{ fontSize: "0.7rem" }}>
              {Math.round((savings / total) * 100)}% kaikesta
            </div>
            <div
              style={{
                height: 2,
                position: "absolute",
                bottom: 0,
                right: 0,
                left: "-300%",
                background: "lime",
                boxShadow: "0 2px 2px rgba(0,0,0,0.2)",
              }}
            />
          </div>
        </div>
      </div>
    </div>
  )
}
